@use '../abstract/' as abs;
@use '../base/' as base;
@use '../layout/' as layout;

// CONTENT - 01 : git fundamental
.content__page--container {
	.nav--container {
		.main__header--01 {
			font-size: 5rem;

			@include abs.mediaQuery(tab-land) {
				font-size: 4.6rem;
			}

			@include abs.mediaQuery(tab-port) {
				font-size: 3.4rem;
			}

			@include abs.mediaQuery(mobile) {
				font-size: 2.9rem;
			}

			@include abs.mediaQuery(mob-small) {
				font-size: 3.3rem;
			}
		}
		///////////
		/// second page
		.main__header--02 {
			font-size: 4.5em;

			@include abs.mediaQuery(tab-land) {
				font-size: 3.8rem;
			}

			@include abs.mediaQuery(tab-port) {
				font-size: 2.8rem;
			}

			@include abs.mediaQuery(mobile) {
				font-size: 2.3rem;
			}

			@include abs.mediaQuery(mob-small) {
				font-size: 2.7rem;
			}
		}

		///////////
		/// THIRD PAGE
		.main__header--03 {
			font-size: 5.6rem;

			@include abs.mediaQuery(tab-land) {
				font-size: 4.7rem;
			}

			@include abs.mediaQuery(tab-port) {
				font-size: 3.6rem;
			}

			@include abs.mediaQuery(mobile) {
				font-size: 3rem;
			}

			@include abs.mediaQuery(mob-small) {
				font-size: 3.4rem;
			}
		}

		.indexes--03 {
			grid-column: 1 / -1;

			a {
				padding-right: 3rem;
				position: relative;

				&:nth-child(4) {
					&:hover {
						transform: scaleX(0.98);
					}
					span {
						letter-spacing: 0;
					}
					&::after {
						position: absolute;
						right: 0;
					}
				}
			}
		}

		////////////
		/// FOUR PAGE
		.main__header--04 {
			font-size: 4.2rem;

			@include abs.mediaQuery(tab-land) {
				font-size: 3.6rem;
			}

			@include abs.mediaQuery(tab-port) {
				font-size: 2.7rem;
			}

			@include abs.mediaQuery(mobile) {
				font-size: 2.25rem;
			}

			@include abs.mediaQuery(mob-small) {
				font-size: 2.6rem;
			}
		}

		//////////////
		/// FIVE PAGE
		.main__header--05 {
			font-size: 5rem;

			@include abs.mediaQuery(tab-land) {
				font-size: 4.5rem;
			}

			@include abs.mediaQuery(tab-port) {
				font-size: 3.4rem;
			}

			@include abs.mediaQuery(mobile) {
				font-size: 2.7rem;
			}

			@include abs.mediaQuery(mob-small) {
				font-size: 3rem;
			}
		}
	}
	.sticky__nav--container {
		@include abs.mediaQuery(tab-land) {
			:nth-child(2) {
				span {
					font-size: 1.5rem;
				}
			}
		}
		// @include abs.mediaQuery(tab-port) {
		// 	.post__navigation__container button,
		// 	button,
		// 	button a {
		// 		display: flex;
