		// 		justify-content: center;

		// 		span {
		// 			margin-top: -1rem;
		// 			line-height: 1.9rem;
		// 			font-size: 1.3rem;
		// 			padding-left: 0;
		// 			padding-right: 0;
		// 		}

		// 		svg {
		// 			height: 5rem;
		// 			width: 100%;
		// 			margin: 1.6rem 0;
		// 		}
		// 	}

		// 	:nth-child(2),
		// 	:nth-child(3) {
		// 		span {
		// 			order: 1;
		// 			font-size: 1.4rem;
		// 		}
		// 		svg {
		// 			order: 2;
		// 		}
		// 	}
		// }
	}
}
